    z-index: 10;
}
.modal-container{
    width: 100%;
    height: 400px;
    overflow: scroll;
    scrollbar-width: none;
}
::-webkit-scrollbar {
    display: none;
}
/* animation effects */
.unActive{
    top: 0% !important; 
    transition: 500ms;
    visibility: hidden;
    opacity: 0;
}


/* close button */
.close{
    color: #aaa;
    float: right;
    font-size: 40px;
    font-weight: bold;
}
.close:hover,.close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}
